export default {
	props: {
		show: {
			type: Boolean,
			default: false 
		},
		isIphoneX: {
			type: Boolean,
			default: false 
		},
		balancetext:{
			type:String,
			default:""
		},
		ordershow:{
			type:String,
			default:""
		}
	},
	data() {
		return {
			password: '',
			trantision: false
		};
	},
	methods: {
		key(key) {
			if (this.password.length < 6) {
				this.password += key;
				if (this.password.length == 6) {
					// console.log(this.password);
					this.$emit("Enterpasswordcompletepayment",this.password)
				}
			}
		},
		clear() {
			this.password = '';
		},
		close() {
			this.password = '';
			this.trantision = false;
			this.$emit('close', false);
		},
		del() {
			if (this.password.length > 0) {
				this.password = this.password.substring(0, this.password.length - 1);
			}
		}
	}
}
